.hotel-collab-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;

    .back-btn {
        .back-icon {
            color: #a37826;

            svg {
                width: 35px;
                height: 35px;
                position: absolute;
                top: 20px;
                left: 20px;
                cursor: pointer;
            }
        }
    }

    .hotel-collab-card-container {
        width: 100%;
        height: 80%;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 40px;

        .hotel-desc-card {
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
            background-color: white;
            position: relative;

            img {
                width: 100%;
                height: 100%;
            }

            .hotel-desc-card-hotelName {
                font-size: 3rem;
                width: 90%;
                color: rgb(199, 199, 199);
                text-transform: uppercase;
                position: absolute;
                bottom: 20px;
                left: 20px;
                margin-right: 20px;
            }

            .hotel-desc-card-fact-container {
                color: black;
                position: absolute;
                padding: 40px 46px;
                height: 100%;

                .hotel-desc-card-fact-title {
                    font-size: 2.2rem;
                    text-transform: uppercase;
                    font-weight: bold;
                    color: red;
                }

                .hotel-desc-card-fact-text {
                    margin-top: 10px;
                    font-size: 1.4rem;
                    text-align: justify;
                    font-weight: 500;
                }
            }

            .hotel-desc-card-info-container {
                color: black;
                position: absolute;
                padding: 40px 46px;
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                height: 100%;
                width: 100%;

                .hotel-desc-card-info-top {
                    display: flex;
                    gap: 20px;
                    justify-content: space-between;

                    .hotel-desc-card-info-top-left,
                    .hotel-desc-card-info-top-right {
                        .hotel-desc-card-info-top-left-title,
                        .hotel-desc-card-info-top-right-title {
                            font-size: 1.3rem;
                            font-weight: bold;
                            text-transform: uppercase;
                        }

                        .hotel-desc-card-info-top-left-text,
                        .hotel-desc-card-info-top-right-text {
                            font-size: 1.2rem;
                            font-weight: 500;
                            margin-top: 10px;
                        }
                    }
                }

                .hotel-desc-card-info-middle,
                .hotel-desc-card-info-bottom {
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;

                    .hotel-desc-card-info-middle-title,
                    .hotel-desc-card-info-bottom-title {
                        font-size: 1.3rem;
                        text-transform: uppercase;
                        font-weight: bold;
                        color: red;
                    }

                    .hotel-desc-card-info-middle-text,
                    .hotel-desc-card-info-bottom-text {
                        width: 100%;
                        font-size: 1.4rem;
                        font-weight: 500;
                    }
                }
            }
        }

        .hotel-desc-card-logo {
            padding: 10px;
        }
    }
}

.hotel-card-hr {
    width: 60%;
    background-color: white;
}

@media (max-width: 1600px) {
    .hotel-collab-container {
        .hotel-collab-card-container {
            .hotel-desc-card {
                .hotel-desc-card-info-container {
                    .hotel-desc-card-info-middle,
                    .hotel-desc-card-info-bottom {
                        .hotel-desc-card-info-middle-title,
                        .hotel-desc-card-info-bottom-title {
                            width: 50%;
                        }

                        .hotel-desc-card-info-middle-text,
                        .hotel-desc-card-info-bottom-text {
                            width: 100%;
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 1270px) {
    .hotel-collab-container {
        height: 140vh;

        .hotel-collab-card-container {
            grid-template-columns: repeat(2, 1fr);
        }
    }
}

@media (max-width: 800px) {
    .hotel-collab-container {
        .hotel-collab-card-container {
            .hotel-desc-card {
                .hotel-desc-card-info-container {
                    padding: 20px 26px;
                }

                .hotel-desc-card-fact-container {
                    padding: 20px 26px;
                }
            }
        }
    }
}

@media (max-width: 750px) {
    .hotel-collab-container {
        height: fit-content;
        border-bottom: none;

        .hotel-collab-card-container {
            display: flex;
            flex-direction: column;
            gap: 10px;
            width: 80%;
            padding: 60px 0;
            position: relative;

            .hotel-collab-card-carousel-overlay {
                height: 260px;
                width: 101%;
                background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.9) 100%);
                z-index: 1;
                position: absolute;
                top: 0;

                .hotel-desc-card-hotelName {
                    font-size: 3rem;
                    color: rgb(226, 226, 226);
                    text-transform: uppercase;
                    position: absolute;
                    bottom: 20px;
                    left: 20px;
                    margin-right: 20px;
                }
            }

            .hotel-desc-card {
                height: 200px;
                width: 100%;

                .hotel-desc-card-fact-container {
                    .hotel-desc-card-fact-text {
                        font-size: 1.7rem;
                    }
                }


                .hotel-desc-card-info-container {
                    height: 200px;

                    .hotel-desc-card-info-middle,
                    .hotel-desc-card-info-bottom {
                        .hotel-desc-card-info-middle-title,
                        .hotel-desc-card-info-bottom-title {
                            font-size: 1.6rem;
                        }

                        .hotel-desc-card-info-middle-text,
                        .hotel-desc-card-info-bottom-text {
                            font-size: 1.6rem;
                        }
                    }
                }
            }
        }
    }

    .slick-slider {
        button {
            display: none !important;
        }
    }
}

@media (max-width: 450px) {
    .hotel-collab-container {
        .hotel-collab-card-container {
            .hotel-desc-card {
                .hotel-desc-card-fact-container {
                    padding: 20px;
                }

                .hotel-desc-card-info-container {
                    padding: 20px;
                }
            }
        }
    }
}
